import jQuery from 'jquery';
// window.$ = window.jQuery = jQuery;

import jqueryMask from 'jquery-mask-plugin';
import validator from 'bootstrap-validator';

jQuery(() => {
    $('input[type="tel"]').mask("+7 (X99) 999-99-99", {
        watchDataMask: true,
        translation: {
            'X': {
                pattern: /9/, 
                optional: false,
            },
        },
    });

    $.fn.validator.Constructor.DEFAULTS.focus = false;

    $('form[data-ajax]').validator().on('submit', function(e) {
        e.preventDefault();

        const form = $(this);
        const formId = form.data('ajax'); 
        const btn = $(form).find('[type="submit"]');

        formData = new FormData(form.get(0));
        formData.append('submit' + formId, 1);

        $.ajax({
            type: 'POST',
            contentType: false,
            processData: false,
            dataType: 'json',
            data: formData,
            success: function(data) {
                console.log('success')
            },
            error: function(data) {
                console.log(data.type);
                console.log('error');
            }
        });
    });
});